@import "fonts";
@import "variables";
@import "libs/normalize";

@import "components/all";

.container {
  min-height: 100vh;
  padding-top: 36px;
  padding-right: 80px;
  padding-bottom: 36px;
  padding-left: 80px;
  background-color: #000;
  background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0.01) 50%, rgba(0, 0, 0, 0.5) 100%), url("../images/bg.jpg");
  background-repeat: repeat;
  background-attachment: fixed;
  background-position: top left;
}

.main-title {
  font-family: "Exo 2", sans-serif;
  font-size: 36px;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 1px #000;
}

.skeleton {
  width: 320px;
  margin-bottom: 40px;
  
  &__root {
    padding-top: 18px;
    padding-right: 16px;
    padding-bottom: 16px;
    padding-left: 54px;
    border-radius: 12px;
    background-color: #f2f2f2;
  }

  &__footer {
    margin-top: 14px;
  }
}